/*Viewports*/
/* open-sans-300 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
/* open-sans-regular - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
/* open-sans-700 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
.page {
  height: 100%;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: #283645;
  text-transform: none;
  background-color: #FFFFFF;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-main__before-triangle + .page-main__triangle::before {
  content: "";
  display: block;
  position: absolute;
  top: -1px;
  left: 0;
  /*  width: 100%; */
  /*   height: 35px; */
  /*  background-image: url(../img/triangle-white-map.png);*/
  background-repeat: no-repeat;
  background-position: bottom;
  border: transparent 0 solid;
  border-bottom: none;
  border-top: #FFFFFF 48px solid;
  border-left: transparent 49vw solid;
  border-right: transparent 49vw solid;
}
@media (min-width: 660px) {
  .page-main__before-triangle + .page-main__triangle::before {
    border-top: #FFFFFF 74px solid;
  }
}
@media (min-width: 1200px) {
  .page-main__before-triangle + .page-main__triangle::before {
    border-top: #FFFFFF 102px solid;
  }
}
.page-footer {
  padding: 35px 20px;
  text-align: center;
}
@media (min-width: 660px) {
  .page-footer {
    display: flex;
    justify-content: space-between;
