.page-footer__icon-block {
  display: block;
  height: 100%;
  width: min-content;
}
.page-footer__icon-block {
  --logo-letters: #283645;
  --logo-glass-stars: #283645;
  --logo-glass: #D22856;
}
.page-footer__icon-block:hover {
